.product-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.product-card__image {
  display: flex;
  height: 271px;
}
.product-card__image img {
  border-radius: 16px;
}
.product-card__title {
  font-size: 16px;
  line-height: 150%;
  font-weight: 700;
  letter-spacing: -0.02em;
}